<template>
  <div class="assetParameter h100">
    <!-- <publicCrumbs :crumbsData="crumbsData"></publicCrumbs> -->
    <div class="page-main-box">
      <KlTable
        v-bind="tableConfig"
        v-show="pageType == 'list'"
        ref="klTable"
      ></KlTable>
      <editPage
        v-if="pageType !== 'list'"
        @goBackPage="goBackPage"
        :rightBtnArr="rightBtnArr"
      >
        <addInfo
          ref="editForm"
          :pageType="pageType"
          :formData="formData"
        ></addInfo>
      </editPage>
    </div>

    <dialogPublic
      title="选择部门"
      :show.sync="showMember"
      @doSubmit="chooseUser"
      @doClose="() => (showMember = false)"
      appendTobody
      width="800px"
      height="400px"
    >
      <orgMemberSelect
        ref="orgMemberSelect"
        v-if="showMember"
        :seleteSetting="orgMemberSelect"
        :otherInfo="otherInfo"
      ></orgMemberSelect>
    </dialogPublic>
  </div>
</template>

<script>
import addInfo from "./add";
import {
  getListApi //获取列表
} from "@/api/itAssets/assetParameter";

export default {
  name: "assetParameter",
  components: { addInfo },
  data() {
    return {
      label: "",
      maxNum: 0,
      otherInfo: "",
      showMember: false,
      currentItem: {},
      orgMemberSelect: {},
      tableConfig: {
        showFixedHeight: true,
        searchObj: {
          searchMethod: (params, that) => {
            if (this.tableConfig.tableDataObj) {
              this.getTableData();
            } else {
            }
          },
          searchArr: [
            {
              type: "el-select",
              label: "资产分类",
              model: "assetTypeId",
              option: this.$store.getters.assetTypeList
            },
            {
              type: "el-input",
              label: "型号",
              model: "goodsModel",
              placeholder: "型号"
            },
            {
              type: "el-input",
              label: "所属人",
              model: "staffunName",
              id: "empId",
              scopeType: "formate",
              method: row => {
                this.chooseStaff("user");
              },
              placeholder: "选择所属人"
            },
            {
              type: "el-input",
              label: "归属部门",
              model: "depatName",
              id: "depatCode",
              scopeType: "formate",
              method: row => {
                this.chooseStaff("dept");
              },
              placeholder: "选择归属部门"
            },
            {
              type: "el-input",
              label: "登记合同号",
              model: "registerNo",
              placeholder: "登记合同号"
            },

            {
              type: "el-select",
              label: "资产状态",
              model: "assetsStatus",
              option: this.$store.getters["dirtData"]("assets_status")
            }
          ]
        },
        // 查询条件参数
        queryObj: {
          method: () => {
            this.getTableData();
          },
          queryData: {
            page: 1,
            pageSize: 15
          }
        },
        tableDataObj: {
          tableArr: [],
          total: null
        },
        operateArr: [
          {
            name: "新建",
            isHide: true,
            // isHide: !this.$store.getters["isBtnAuth"]("insert"),
            icon: "el-icon-plus",
            method: name => {
              this.pageType = "add";
            }
          }
        ],
        //  table表配置参数
        tableObj: {
          // 左边选择框展示
          selectionShow: false,
          // 左边序号展示
          indexShow: true,
          // table表事件参数配置
          tableMethods: {
            selection: val => {
              this.selectionData = val;
            }
          },
          // table表展示列配置
          showItems: [
            {
              name: "IT资产编号",
              prop: "assetsNumber"
              // width: 150
              // align: "left",
            },
            {
              name: "固定资产编码",
              prop: "fixedNumber"
            },
            {
              name: "资产分类",
              prop: "assetTypeName",
              id: "assetTypeId"
              // method: (row) => {
              //   let value = row.assetTypeId;
              //   return (
              //     this.$store.getters["fineDirtDataName"](
              //       "TEACH_RECORD_TYPE",
              //       value
              //     ) || value
              //   );
              // },
            },
            {
              name: "品牌",
              prop: "brand",
              id: "brand",
              scopeType: "formate",
              method: row => {
                let value = row.brand;
                return (
                  this.$store.getters["fineDirtDataName"](
                    "brand_type",
                    value
                  ) || value
                );
              }
              // width: 100,
            },
            {
              name: "资产型号",
              prop: "goodsModel"
            },
            {
              name: "所有人",
              prop: "staffunName",
              id: "empId"
              // width: 70
            },
            {
              name: "归属部门",
              id: "depatCode",
              prop: "depatName"
              // width: 140
            },
            {
              name: "登记合同",
              prop: "registerNo"
              // width: 150
              // align: "left"
            },
            {
              name: "资产状态",
              prop: "assetsStatus",
              scopeType: "formate",
              method: row => {
                let value = row.assetsStatus;
                return (
                  this.$store.getters["fineDirtDataName"](
                    "assets_status",
                    value
                  ) || value
                );
              }
              // width: 70
            },
            {
              name: "登记状态",
              prop: "regisStatus",
              scopeType: "formate",
              method: row => {
                let value = row.regisStatus;
                return (
                  this.$store.getters["fineDirtDataName"](
                    "regis_status",
                    value
                  ) || value
                );
              }
            },
            {
              name: "转移单号",
              prop: "assShiftNo"
            },
            {
              name: "报废单号",
              prop: "scrappedNo"
            },
            {
              name: "操作",
              prop: "operate",
              width: 100,
              scopeType: "button",
              fixed: "right",
              buttonArr: [
                //最后一行操作栏数组
                {
                  name: "详情",
                  method: row => {
                    this.detailsInfo(row);
                  },
                  isShow: (row, that) => {
                    return true;
                    // return this.$store.getters["isBtnAuth"]("details");
                  },
                  icon: "false"
                }
              ]
            }
          ]
        },
        // 分页展示总条配置
        paginationObj: {
          pageSizes: [15, 30, 50, 100]
        },
        paginationIsshow: true,
        showklTable: false
      },
      pageType: "list",
      selectType: null,
      formData: {}
    };
  },
  watch: {
    pageType(val) {
      if (val === "list") {
        this.getTableData();
      }
    }
  },
  computed: {
    rightBtnArr() {
      let arr = [
        {
          text: "保存",
          icon: "el-icon-finished",
          hide: this.pageType == "details",
          methods: () => {
            this.doSubmit();
          }
        }
      ];
      return arr;
    }
  },

  mounted() {
    this.getTableData();
  },
  methods: {
    //查询数据
    getTableData() {
      let form = Object.assign({}, this.tableConfig.queryObj.queryData);
      // this.$delete(form, "staffunName");//去掉人的中文名
      // this.$delete(form, "depatName");//去掉部门的中文名
      form.pageCount = form.pageSize;
      this.$delete(form, "pageSize");
      getListApi(form)
        .then(res => {
          this.$set(
            this.tableConfig.tableDataObj,
            "tableArr",
            res.data.list || []
          );
          this.$set(
            this.tableConfig.tableDataObj,
            "total",
            res.data.total || 0
          );
        })
        .catch(res => {
          this.$set(this.tableConfig.tableDataObj, "tableArr", []);
          this.$set(this.tableConfig.tableDataObj, "total", 0);
        });
    },
    //选择组织人员
    chooseStaff(type) {
      //对类型进行判断
      let typePre = type.split("-")[0]; //分割type
      let typeBre = type.split("-")[1];
      this.selectType = typePre;
      if (typePre == "dept") {
        //组织选择

        this.orgMemberSelect = {
          seleteType: typeBre ? typeBre : "org",
          radio: typePre == "dept", //单选
          props: {
            label: "organizationName",
            children: "children"
          },
          column: [
            {
              label: "组织",
              prop: "name"
            }
          ],
          chosedData: []
        };
      } else if (typePre == "user") {
        //人员选择
        this.orgMemberSelect = {
          seleteType: typeBre ? typeBre : "orgmember",
          radio: typePre == "user", //单选
          props: {
            label: "organizationName",
            children: "children"
          },
          column: [
            {
              label: "姓名",
              prop: "name",
              width: 60
            }
          ],
          chosedData: []
        };
      }
      this.showMember = true;
    },

    //确定选择人员
    chooseUser() {
      let data = this.$refs.orgMemberSelect.getSeleteData();
      let newData = data[0];
      let names = "";
      let ids = "";
      if (this.selectType == "dept") {
        if (newData) {
          this.$set(
            this.tableConfig.queryObj.queryData,
            "depatName",
            newData.name
          );
          this.$set(
            this.tableConfig.queryObj.queryData,
            "depatCode",
            newData.id
          );
        }
      } else if (this.selectType == "user") {
        if (newData) {
          this.$set(
            this.tableConfig.queryObj.queryData,
            "staffunName",
            newData.name
          );
          this.$set(this.tableConfig.queryObj.queryData, "empId", newData.id);
        }
      }

      this.showMember = false;
    },

    //确定新增
    addSeleteMember() {
      let $Vue = this.$Vue;
      let data = this.selections;
      $Vue.$refs["tabs" + this.tabname][0].setTableData(data);
      this.showMember = false;
    },
    //详情
    detailsInfo(row) {
      this.pageType = "details";
      this.formData = Object.assign({}, row);
    },

    //点击提交
    doSubmit() {
      this.$refs.editForm
        .getFormData()
        .then(data => {
          this.addMethons(data).then(res => {
            this.$message.success("操作成功！");
            if (!data.id) {
              this.formData = { id: res.data };
            }
            this.pageType = "list";
            this.getTableData();
            // this.pageType = "edit";
            // this.$refs.editForm.getDetails(this.formData.id);
          });
        })
        .catch(e => {
          console.log(e);
        });
    },
    //新增
    addMethons(data) {
      this.$delete(data, "staffunName"); //删除采购人名字
      if (data.id) {
        //有ID则为修改
        return new Promise((resolve, reject) => {
          updateRegist(data)
            .then(res => {
              resolve(res);
            })
            .catch(e => {
              console.log(e);
              reject(e);
            });
        });
      } else {
        //新增
        return new Promise((resolve, reject) => {
          addRegist(data)
            .then(res => {
              resolve(res);
            })
            .catch(e => {
              console.log(e);
              reject(e);
            });
        });
      }
    },
    //返回
    goBackPage(data) {
      this.pageType = "list";
    }
  }
};
</script>

