<template>
  <div class="record__content">
    <div class="record__baseinfo">
      <dividerTitle :title="'基本信息'"></dividerTitle>
      <editForm
        :formConfig="formConfig"
        ref="editForm"
        :formData="editFormData"
      >
      </editForm>
    </div>

  </div>
</template>

<script>
// import { uploadAttach } from "@/api/common";
export default {
  props: {
    formData: {
      type: Object,
      default: new Object()
    },
    pageType: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      editFormData: { suitableWorkArr: [] },
      trainingType: {},
      formConfig: {
        itemArr: [
          {
            label: "IT资产编号",
            prop: "assetsNumber",
            type: "text",
            // required: true,
            span: 8
          },
          {
            label: "固定资产编码",
            prop: "fixedNumber",
            // required: true,
            type: "text"
          },
          {
            label: "供应商",
            prop: "companyName",
            idsStr: "companyId",
            type: "autocomplete",
            trigger: "change",
            placeholder: "可输入关键词查找供应商",
            seleteOption: this.$store.getters.supplierList
          },
          {
            type: "select",
            label: "资产分类",
            prop: "assetTypeId",
            seleteOption: this.$store.getters.assetTypeList
          },
          {
            label: "品牌",
            prop: "brand",
            id: "brand",
            type: "text"
          },
          {
            label: "资产型号",
            prop: "goodsModel",
            type: "text"
          },

          {
            label: "所有人",
            prop: "staffunName",
            idsStr: "empId",
            type: "user",
            trigger: "change"
          },
          {
            label: "归属部门",
            prop: "staffunName",
            idsStr: "depatCode",
            type: "dept",
            trigger: "change"
          },

          {
            label: "登记合同",
            prop: "registerNo",
            type: "text"
          },
          {
            label: "资产状态",
            prop: "assetsStatus",
            type: "select",
            seleteOption: this.$store.getters["dirtData"]("assets_status"),
            formatter: val => {
              return this.$store.getters["fineDirtDataName"](
                "assets_status",
                val
              );
            }
          },
          {
            label: "登记状态",
            prop: "regisStatus",
            type: "text"
          },
          {
            label: "转移单号",
            prop: "assShiftNo",
            type: "text"
          },
          {
            label: "报废单号",
            prop: "scrappedNo",
            type: "text"
          }
        ]
      },

      coverList: [],
      dialogVisible: false,
      //
      activeName: "0"
    };
  },
  computed: {},
  created() {
    this.init();
  },
  methods: {
    init() {
      if (this.pageType != "add") {
        this.editFormData = Object.assign({}, this.formData);
      } else {
        this.editFormData = Object.assign({}, this.formData);
      }
      if (this.pageType == "details") {
        this.formConfig.itemArr.map(i => (i.disabled = true));
      }
    },

    //获取表单数据
    getFormData() {
      let This = this;
      return new Promise((resolve, reject) => {
        this.$refs.editForm
          .getFormData()
          .then(data => {
            data = Object.assign({}, data);
            resolve(data);
          })
          .catch(e => {
            reject(e);
          });
      });
    },
    getDetails() {}
  }
};
</script>

